import { isPlatformBrowser } from '@angular/common';
import { Component, Inject, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { AisleType, SectionType } from '@core/enums';
import { Seller } from '@core/models';
import { IntercomService } from '@core/services/intercom.service';
import { StructuredDataService } from '@core/services/structured-data.service';
import { AuthFacade } from '@feature/auth/auth.facade';
import { DestroyService } from '@shared/services/destroy.service';
import { Observable, takeUntil } from 'rxjs';
import { HomeFacade } from './home.facade';

@Component({
	templateUrl: './home.page.html',
	styleUrls: ['./home.page.scss'],
	standalone: false,
	providers: [DestroyService],
})
export class HomePage {
	constructor(
		@Inject(PLATFORM_ID) private readonly platformId: string,
		private readonly homeFacade: HomeFacade,
		private readonly route: ActivatedRoute,
		private readonly intercom: IntercomService,
		private readonly structuredDataService: StructuredDataService,
		private readonly authFacade: AuthFacade,
		private readonly destroyService: DestroyService,
	) {}

	vm$ = this.homeFacade.getHomeVm$();
	bussinessListVm$: Observable<Seller[]>;
	routeSection: 'rent' | 'share' | 'sale' = 'rent';

	readonly sectionType = SectionType;
	readonly aisleType = AisleType;
	readonly appRoute = APP_ROUTE;
	readonly isPlatformBrowser = isPlatformBrowser(this.platformId);

	readonly themeListMobile = { border: '4', width: '100%', height: '220px' };
	readonly themeList = Array(3).fill({ border: '4', width: '100%', margin: '32px', height: '266px' });

	//to check the invitation link
	private checkForAgentEmailInvitation(): void {
		if (!this.isPlatformBrowser) return;

		this.route.queryParams.pipe(takeUntil(this.destroyService)).subscribe((queryParams) => {
			const { token, reference, parentToken, type, authToken, purpose } = queryParams;

			if (token && reference && parentToken) {
				const EMAIL_INVITATION_DATA: any = { token, reference, parentToken };

				if (type) EMAIL_INVITATION_DATA.type = type;

				if (this.homeFacade.isNewEmailInvitation(EMAIL_INVITATION_DATA)) {
					this.homeFacade.setEmailInvitation(EMAIL_INVITATION_DATA);

					if (type) {
						this.authFacade.acceptAgentInvitation(EMAIL_INVITATION_DATA, undefined, purpose);
					} else {
						this.homeFacade.verifyEmailInvitation(EMAIL_INVITATION_DATA);
					}
				}
			} else if (authToken && type) {
				this.authFacade.instantLoginAndRedirect(queryParams);
			}
		});
	}

	ngOnInit(): void {
		this.route.data.pipe(takeUntil(this.destroyService)).subscribe((res) => {
			this.routeSection = res?.['section'] ?? 'sale';

			// this.bussinessListVm$ = this.homeFacade.getBusiness$();

			// this.structuredDataService.generateWebsite(this.routeSection);
			// this.homeFacade.loadAisles(this.routeSection === 'sale' ? SectionType.PROPERTIES_FOR_SALE : SectionType.PROPERTIES_FOR_RENT);
			// this.homeFacade.loadLatestAdsList();
			// this.checkForAgentEmailInvitation();
		});

		if (isPlatformBrowser(this.platformId)) {
			this.intercom.bootIntercom();
		}
	}

	ngOnDestroy(): void {
		if (isPlatformBrowser(this.platformId)) {
			this.intercom.shutdownIntercom();
		}

		this.structuredDataService.destroyScript();
	}
}
