import { Injectable } from '@angular/core';
import { FiltersApi } from '@core/data-access/apis/filters.api';
import { SectionsState } from '@core/data-access/states/sections.state';
import { AisleType, SectionType } from '@core/enums';
import { Aisle, FilterValue, Section } from '@core/models';
import { BehaviorSubject, Observable, filter, map } from 'rxjs';

@Injectable()
export class FiltersTabsFacade {
	constructor(
		private readonly filtersApi: FiltersApi,
		private readonly sectionsState: SectionsState
	) {}

	private readonly _countyList$ = new BehaviorSubject<FilterValue>(undefined);

	getCountyList$(): Observable<FilterValue> {
		return this._countyList$.asObservable();
	}

	getRentAisleList$(): Observable<Aisle[]> {
		return this.sectionsState.sectionList$.pipe(
			filter((sectionList: Section[]) => !!sectionList?.length),
			map(() => {
				const AISLE_LIST = this.sectionsState.getSection(SectionType.PROPERTIES_FOR_RENT).aisles;

				return AISLE_LIST.filter((aisle: Aisle) => aisle.reference === AisleType.RENT_RESIDENTIAL || aisle.reference === AisleType.RENT_COMMERCIAL);
			})
		);
	}

	getBuyAisleList$(): Observable<Aisle[]> {
		return this.sectionsState.sectionList$.pipe(
			filter((sectionList: Section[]) => !!sectionList?.length),
			map(() => this.sectionsState.getSection(SectionType.PROPERTIES_FOR_SALE)?.aisles || [])
		);
	}

	loadCountyList(): void {
		this.filtersApi.getFilterValue('county').subscribe((countyList: FilterValue) => this._countyList$.next(countyList));
	}
}
