import { isPlatformBrowser } from '@angular/common';
import { Component, Inject, Input, PLATFORM_ID } from '@angular/core';
import { HeaderSidenavComponent } from '@core/components/header/components/header-sidenav/header-sidenav.component';
import { APP_ROUTE } from '@core/constants';
import { OFFCANVAS_HEADER_SIDENAV } from '@core/constants/offcanvas.constant';
import { AuthService } from '@core/data-access/services/auth.service';
import { SectionType } from '@core/enums';
import { CommonFacade } from '@core/facades/common.facade';
import { AmplitudeService } from '@core/services/amplitude.service';
import { CommonPlaceAdService } from '@core/services/common-place-ad.service';
import { IntercomService } from '@core/services/intercom.service';
import { RouteService } from '@core/services/route.service';
import { ViewportService } from '@core/services/viewport.service';
import { HomeTab, HomeTabService } from '@feature/home/services/home-tab.service';
import { NgbOffcanvas } from '@ng-bootstrap/ng-bootstrap';
import { ButtonType } from '@shared/components/button/button.component';
import { DestroyService } from '@shared/services/destroy.service';

@Component({
	selector: 'findqo-home-banner-v2',
	templateUrl: './home-banner-v2.component.html',
	styleUrls: ['./home-banner-v2.component.scss'],
	providers: [DestroyService],
	standalone: false,
})
export class HomeBannerv2Component {
	constructor(
		@Inject(PLATFORM_ID) readonly platformId: string,
		public readonly authService: AuthService,
		private readonly commonFacade: CommonFacade,
		private readonly homeTabService: HomeTabService,
		private readonly routeService: RouteService,
		private intercomService: IntercomService,
		private ngbOffCanvas: NgbOffcanvas,
		public viewport: ViewportService,
		private amplitude: AmplitudeService,
		private commonPlaceAdService: CommonPlaceAdService,
	) {}

	@Input() section: 'rent' | 'share' | 'sale';
	@Input() containerClass = '';
	@Input() isFilterDialog = false;

	readonly isPlatformBrowser = isPlatformBrowser(this.platformId);

	user$ = this.commonFacade.getUser$();
	routeData$ = this.routeService.routeData$;
	sectionList$ = this.commonFacade.getSectionList$();
	selectedTab = HomeTab.RENT;

	readonly buttonType = ButtonType;
	readonly appRoute = APP_ROUTE;
	readonly tab = HomeTab;

	onLogin(): void {
		this.authService.openAuthDialog();
	}

	goToPlaceAd(): void {
		this.amplitude.trackPlaceAdButtonClicked('Place FREE Ad');
		this.commonPlaceAdService.gotoPlaceAd();
	}

	onTabChange(tab: HomeTab): void {
		const SECTION = tab === HomeTab.BUY ? SectionType.PROPERTIES_FOR_SALE : SectionType.PROPERTIES_FOR_RENT;

		this.selectedTab = tab;
		this.homeTabService.setSelectedSection(SECTION);
	}

	onOpenHeaderSidenav(): void {
		this.intercomService.toggelIntercomPopup(true);
		this.ngbOffCanvas.open(HeaderSidenavComponent, OFFCANVAS_HEADER_SIDENAV);
	}
}
