import { Injectable } from '@angular/core';
import { SectionType } from '@core/enums';
import { BehaviorSubject, Observable } from 'rxjs';

export enum HomeTab {
	RENT,
	SHARE,
	BUY,
}

@Injectable()
export class HomeTabService {
	private readonly _selectedSection$ = new BehaviorSubject<SectionType>(SectionType.PROPERTIES_FOR_RENT);

	get selectedSection$(): Observable<SectionType> {
		return this._selectedSection$.asObservable();
	}

	setSelectedSection(section: SectionType): void {
		this._selectedSection$.next(section);
	}
}
