import { Component, EventEmitter, Output, PLATFORM_ID, Inject, Input } from '@angular/core';
import { AbstractControl, FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { AisleType, CountyType, FilterType, SectionType } from '@core/enums';
import { CommonFacade } from '@core/facades/common.facade';
import { HomeTab } from '@feature/home/services/home-tab.service';
import { FiltersTabsFacade } from './filters-tabs.facade';
import { FiltersService } from '@feature/general/filters/services/filters.service';
import { isPlatformBrowser } from '@angular/common';

@Component({
    selector: 'findqo-filters-tabs',
    templateUrl: './filters-tabs.component.html',
    styleUrls: ['./filters-tabs.component.scss'],
    standalone: false
})
export class FiltersTabsComponent {
	constructor(
		@Inject(PLATFORM_ID) private platformId: string,
		private readonly fb: FormBuilder,
		private readonly commonFacade: CommonFacade,
		private readonly filtersTabsFacade: FiltersTabsFacade,
		public readonly router: Router,
		private readonly filterService: FiltersService
	) {}

	@Output() tabChange = new EventEmitter<HomeTab>();

	@Input() set section(value: 'rent' | 'share' | 'sale') {
		const index = value === 'sale' ? 2 : value === 'rent' ? 0 : 1;

		this.initDeclarations();
		this.routeSection = value;

		this.onSectionTabChange(index);
	}

	routeSection: 'rent' | 'share' | 'sale';
	aisleList$ = this.commonFacade.getAiseList$(SectionType.PROPERTIES_FOR_RENT);
	countyList$ = this.filtersTabsFacade.getCountyList$();
	rentAisleList$ = this.filtersTabsFacade.getRentAisleList$();
	buyAisleList$ = this.filtersTabsFacade.getBuyAisleList$();
	recentSearches$ = this.commonFacade.recentSearches$;

	rentForm: FormGroup;
	shareForm: FormGroup;
	buyForm: FormGroup;

	selectedTab = HomeTab.RENT;
	headingTitle = 'Properties to rent in Ireland';

	readonly aisleType = AisleType;

	get rentFc(): { [p: string]: AbstractControl } {
		return this.rentForm.controls;
	}

	get shareFc(): { [p: string]: AbstractControl } {
		return this.shareForm.controls;
	}

	get buyFc(): { [p: string]: AbstractControl } {
		return this.buyForm.controls;
	}

	get locations(): FormArray {
		return this.filterService.locations;
	}

	onGotoAdList(): void {
		const PARAMS = this.formMapper(this.selectedTab).value;
		const UNIQUES_LOCATIONS = [...new Set(this.locations.value.join(',').split(','))];
		const [FIRST_LOCATION, ...OTHER_LOCATIONS] = UNIQUES_LOCATIONS;
		const COMMANDS = [PARAMS[FilterType.SECTION], FIRST_LOCATION ? FIRST_LOCATION : CountyType.DEFAULT, PARAMS[FilterType.AISLE]];

		if (PARAMS[FilterType.STAND]) {
			COMMANDS.push(PARAMS[FilterType.STAND]);
		}

		this.router.navigate([...COMMANDS], { queryParams: OTHER_LOCATIONS.length ? { location: OTHER_LOCATIONS.join(',') } : {} });
	}

	onSectionTabChange(index: number): void {
		this.aisleList$ = this.commonFacade.getAiseList$(index === HomeTab.BUY ? SectionType.PROPERTIES_FOR_SALE : SectionType.PROPERTIES_FOR_RENT);
		this.selectedTab = index;
		this.headingTitle =
			index === HomeTab.BUY
				? 'Search Properties for Sale in Ireland â€“ Houses, Apartments, and Commercial Spaces'
				: index === HomeTab.RENT
					? 'Find Apartments, Houses, and Offices for Rent in Ireland'
					: 'Affordable Shared Accommodations and Rooms for Rent in Ireland';

		this.tabChange.emit(index);
	}

	private formMapper(tab: HomeTab): FormGroup {
		const FORM_MAPPER = {
			[HomeTab.RENT]: this.rentForm,
			[HomeTab.SHARE]: this.shareForm,
			[HomeTab.BUY]: this.buyForm,
		};

		return FORM_MAPPER[tab];
	}

	private createRentForm(): void {
		this.rentForm = this.fb.group({
			[FilterType.SECTION]: [SectionType.PROPERTIES_FOR_RENT],
			[FilterType.AISLE]: [AisleType.RENT_RESIDENTIAL],
			[FilterType.STAND]: [''],
			[FilterType.LOCATION_REFERENCE]: [''],
		});
	}

	private createShareForm(): void {
		this.shareForm = this.fb.group({
			[FilterType.SECTION]: [SectionType.PROPERTIES_FOR_RENT],
			[FilterType.AISLE]: [AisleType.SHARE_RESIDENTIAL],
			[FilterType.STAND]: [''],
			[FilterType.LOCATION_REFERENCE]: [''],
		});
	}

	private createBuyForm(): void {
		this.buyForm = this.fb.group({
			[FilterType.SECTION]: [SectionType.PROPERTIES_FOR_SALE],
			[FilterType.AISLE]: [AisleType.SALE_RESIDENTIAL],
			[FilterType.STAND]: [''],
			[FilterType.LOCATION_REFERENCE]: [''],
		});
	}

	private initDeclarations(): void {
		if (isPlatformBrowser(this.platformId)) {
			this.fetchRecentSearch();
		}

		// Create forms
		this.createRentForm();
		this.createShareForm();
		this.createBuyForm();
	}

	fetchRecentSearch(): void {
		this.commonFacade.fetchRecentSearch();
	}
}
